import { chat, messageFormatting } from '@sillytavern/script';
import { getLastMessageId } from '@sillytavern/scripts/macros';


/**
 *  html . ,
 * 1. 
 * 2. 
 * 3.  html 
 *
 * @param text 
 * @param option 
 *   - `message_id?:number`: , ,  `[0, await getLastMessageId()]` ; 
 *
 * @returns 
 */
export function formatAsDisplayedMessage(text: string, option: FormatAsDisplayedMessageOption = {}): string {
  option.message_id = option.message_id ?? 'last';
  if (typeof option.message_id !== 'number' && !['last', 'last_user', 'last_char'].includes(option.message_id)) {
    throw Error(
      ` message_id ,  'last', 'last_user', 'last_char' , : ${option.message_id}`,
    );
  }
  const last_message_id = getLastMessageId();
  if (last_message_id === null) {
    throw Error(`, : ${option.message_id}`);
  }
  switch (option.message_id) {
    case 'last':
      option.message_id = last_message_id;
      break;
    case 'last_user': {
      const last_user_message_id = getLastMessageId({ filter: (m: any) => m.is_user && !m.is_system }) as number;
      if (last_user_message_id === null) {
        throw Error(` user , : ${option.message_id}`);
      }
      option.message_id = last_user_message_id;
      break;
    }
    case 'last_char': {
      const last_char_message_id = getLastMessageId({ filter: (m: any) => !m.is_user && !m.is_system }) as number;
      if (last_char_message_id === null) {
        throw Error(` char , : ${option.message_id}`);
      }
      option.message_id = last_char_message_id;
      break;
    }
  }
  if (option.message_id < 0 || option.message_id > last_message_id) {
    throw Error(` message_id  [0, ${last_message_id}] , : ${option.message_id} `);
  }

  const chat_message = chat[option.message_id];
  const result = messageFormatting(
    text,
    chat_message.name,
    chat_message.is_system,
    chat_message.is_user,
    option.message_id,
  );

  console.info(
    ` html , : '${text}', : '${JSON.stringify(option)}', : '${result}'`,
  );
  return result;
}
